import React from 'react';
import {connect} from "react-redux"
import { push } from 'connected-react-router'

import {makeStyles,fade} from "@material-ui/core/styles"

import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import Container from "@material-ui/core/Container"

import SearchIcon from '@material-ui/icons/Search';

//import {add_history} from "../history/actions"

const useStyles = makeStyles((theme)=>({
    root:{
      marginTop:theme.spacing(10),
      width:"100%"
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
          backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginLeft: "auto",
        marginRight: "auto",
        width: '100%',
        [theme.breakpoints.up('sm')]: {
          //marginLeft: theme.spacing(1),
          width: '60%',
          marginLeft: "auto",
          marginRight: "auto",
        },
      },
      searchIcon: {
        width: theme.spacing(7),
        color: "#FFF",
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      },
      inputRoot: {
        color: 'inherit',
        width: '100%'
      },
      inputInput: {
        padding: theme.spacing(1, 1, 1, 7),
        color :"#FFF",
        //transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
          width: "100%",
          /*'&:focus': {
            width: 200,
          },*/
        },
      },
}))

const SearchBar = ({dispatch})=>{
    const classes = useStyles()
    let input
    return (
        <React.Fragment>
            <div className={classes.root}>
              <Container>
                <Toolbar>
                    <div className={classes.search}>
                        <form onSubmit={(e)=>{
                            e.preventDefault()
                            const search_text = input.value
                            input.value = ""
                            //dispatch(add_history(search_text))
                            //dispatch(push("/search/"+search_text))
                            document.location.href="https://www.wiretechnology.net/index.html?q="+search_text
                        }}>
                            <div className={classes.searchIcon}>
                                <SearchIcon/>
                            </div>
                            <InputBase
                                placeholder="Search..."
                                classes={{
                                    root:classes.inputRoot,
                                    input:classes.inputInput
                                }}
                                inputProps={{ 'aria-label': 'search' }}
                                inputRef={(node)=>{input=node}}
                            />
                        </form>
                    </div>
                </Toolbar>
                </Container>
            </div>
        </React.Fragment>
    )
}

export default connect()(SearchBar) 