import {layers} from '@tensorflow/tfjs';
import * as tf from '@tensorflow/tfjs-core';
// import {} from '@tensorflow/tfjs-core';


class HyperScale extends layers.Layer{
    constructor(config){
        super(config);
        this.value = config["value"]
    }

    computeOutputShape(inputShape) {
        return [inputShape[0], inputShape[1], inputShape[2], inputShape[3]]
      }

      call(inputs, kwargs) {
        let input = inputs;
        if (Array.isArray(input)) {
          input = input[0];
        }
        this.invokeCallHook(inputs, kwargs);
        var res = tf.mul(input,tf.scalar(this.value))
        
        //res.print()
        return res
      }

      static get className() {
        return 'HyperScale';
      }
      
      getConfig(){
        const config = {
          value:this.value
        }
        const baseConfig = super.getConfig();
        Object.assign(config, baseConfig);
        return config;
      }

}

export default HyperScale